* {
    margin: 0px;
    padding: 0px;
    font-family: 'Ubuntu', sans-serif;
}
.table{
 /*    position: fixed;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%); */
   position: fixed;
   width: 100vw;
   height: 100vh;
    background-image: url('../img/table.jpeg');
}
.calBody{
    position: fixed;
    top: 50%;
    left: 50%;
    transition: all 0.5s ease;
    transform: translate(-50%, -50%);
    /* background-color: black; */
    background: rgb(159,157,157);
    background: linear-gradient(270deg, rgba(159,157,157,1) 0%, rgba(125,125,125,1) 1%, rgba(71,71,71,1) 3%, rgba(0,0,0,1) 5%);
   /*  height: 370px; */
    width: 260px;
    color: white;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.75);
    border: 4px outset;

}
.calBody:hover{
    transform: translate(-50%, -50%) scale(1.2);
}
.screen{
    background: rgb(225,225,225);
    background: linear-gradient(270deg, rgba(225,225,225,1) 0%, rgba(163,163,163,1) 54%, rgba(131,130,138,1) 97%, rgba(2,0,36,1) 100%);
    color: black;
    width: 100%;
    display: inline-block;
    padding: 10px 20px;
    box-sizing: border-box;
    text-align: end;
    border-radius: 5px;
    box-shadow: 2px 1px 2px 2px #9d9d9d;
    line-height: 32px;
    word-wrap: break-word;
    font-family: 'Ubuntu', sans-serif;
    margin-bottom: 30px;
    border: 1px inset;
}
.screen p{
    font-size: 30px;
    overflow-wrap: break-word;
}
.buttons{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: 
        " number number number methods";
    /* grid-template-rows: repeat(2, 1fr); */
    grid-gap: 10px;
    height: 100%;
    padding: 0px 0px;
}
.numbers{
    grid-area: number;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    cursor: pointer;
    color: black;
    align-content: center;
    text-align: center;
    grid-gap: 10px;
    grid-template-rows: repeat(4, 1fr) min-content;
}
.numbers div{
    background: rgb(2,0,36);
    background: linear-gradient(
271deg, rgba(2,0,36,1) 0%, rgba(225,225,225,1) 0%, rgba(255,255,255,1) 29%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px #ccc;
    font-size: 25px;
    border: 1px outset;
}
.numbers div:hover{
    background: linear-gradient( 
        271deg, rgb(0 0 0) 0%, rgb(98 92 92) 0%, rgb(137 137 137) 29%);
}
.methods{
    grid-area: methods;
    grid-gap: 10px;
    display: grid;
    text-align: center;
    padding-bottom: 10px;
    font-size: 30px;
}
.methods div{
    cursor: pointer;
    background: rgb(2,0,36);
    background: linear-gradient(270deg, rgba(2,0,36,1) 0%, rgba(203,138,18,1) 0%, rgba(255,165,0,1) 57%);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px #ffc94a;
    border: 1px outset;
}
.methods div:hover{
    background: linear-gradient(
270deg, rgba(2,0,36,1) 0%, rgb(106 74 15) 0%, rgb(171 128 50) 57%);
}